specto se considera el éxito del clima organizacional.")])])])]),_c('div',{attrs:{"titulo":"Seguridad y Salud en el Trabajo SG-SST"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-2 col-5 px-lg-2 mb-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3-ico6.svg"),"alt":"Seguridad y Salud en el Trabajo SG-SST"}})])]),_c('div',{staticClass:"col-md-9 col-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Desde el punto de vista holístico, el hecho de que una persona pueda desarrollar sus labores de forma segura le permitirá estar equilibrado; de igual manera, el cuerpo y la mente alineados le concede al talento humano que coordine sus labores de forma concentrada y que el impacto a nivel físico y emocional de estar varias horas en un lugar de trabajo sea el mínimo. Las posturas, la protección física, la iluminación, la ergonomía, el manejo del estrés, la presión laboral son aspectos a los que se les hace seguimiento mediante esta área. Se evalúan, además, las condiciones de la infraestructura, los riesgos y peligros a los que se pueden enfrentar los colaboradores en su labor.")])])])]),_c('div',{attrs:{"titulo":"Supervisión del talento humano"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-2 col-5 px-lg-2 mb-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3-ico7.svg"),"alt":"Supervisión del talento humano"}})])]),_c('div',{staticClass:"col-md-9 col-12 mb-4 mb-md-0"},[_c('p',[_vm._v("La supervisión corresponde al seguimiento del talento humano, no con el ánimo verificar o controlar que cumpla con su trabajo, sino con el ánimo de acompañar, orientar y mantener un clima organizacional adecuado. A través de la supervisión se puede monitorear el cumplimiento de los objetivos trazados.")])])])]),_c('div',{attrs:{"titulo":"Evaluación de la administración del talento humano"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-2 col-5 px-lg-2 mb-3